*:focus{
    outline: none;
}
body{
    background: #171616;   
    background-size :400% 400%;
}
@keyframes move_bg{
    0%{background-position: 0% 0%;}
    25%{background-position: 50% 50%;}
    50%{background-position: 100% 100%;}
    75%{background-position: 50% 50%;}
    100%{background-position: 0% 0%;}
}
.center_header{
    background: #1160c1;
    width: 100%;
    height: 50px;
    /* margin-top: 12px; */
    box-shadow: 0px -1px 4px 0px rgb(183, 181, 181);
}
.center_div{
    border-radius: 15px;
    width: 50%;
    height: 70%!important;
    position: absolute;
    /* border: 3px solid transparent; */
    background: rgb(93, 93, 93);
    left: 25%;
    top: 15%;
    box-shadow: 0 0 6px 1px white;
}
.lang_select_div{
    width: 100%;
    height: 50px;
    background-color: transparent;
}
.language_select{
    font-family: monospace;
    width: 70%;
    /* text-align: right; */
    word-spacing: -2px;
    font-weight: 600;
    height: 50%;
    background: #1babff;
    color: white;
    text-align: center;
    letter-spacing: 0px;
    margin: 0 15%;
    font-size: 16px;
}
p{
    font-weight: bolder;
    margin: 0 auto;
    width: 50%;
    display: block;
    height: 50%;
    font-family: monospace;
    font-size: 20px;
    color: #0037ff;
    letter-spacing: 0px;
}
.lang_div{
    width: 50%;
    left: 0;
    height: 50px;
    position: absolute;

}
input[type="text"]{
    margin-top: 17px;
    margin-left: 17%;
    margin-right: 5%;
    color: white;
    background: #3e3c3c;
    border: none;
    border-left: 2px solid #fff;
    height: 6%;
    position: static;
}
#temp{
    font-weight: bolder;
    display: inline-block;
    height: 6%;
    width: 4%;
    font-family: monospace;
    font-size: 20px;
    color: #e0e0e0;
    letter-spacing: 0px;
}
#record_button{
    border: none;
    border-bottom: 1px solid;
    /* outline: 1px solid; */
    font-family: monospace;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.6;
    border-radius: 7px;
    width: 127px;
    margin-left: 5%;
    height: 6%;
    color: rgb(191, 190, 190);
    margin-top: 17px;
    background: #3e3c3c;
}
#translated_text{
    letter-spacing: 0.8;
    margin: 39px auto;
    font-family: monospace;
    width: 47%;
    height: 120px;
    color: white;
    background-color: rgb(62, 60, 60);
    padding-block-start: 5px;
    border-radius: 5px;
    padding: 8px;
}
audio{
    width: 40%;
    height: 20px;
}
#heading{
    background: linear-gradient(30deg , white, blue);
    -webkit-background-clip: text;
    margin: auto;
    margin-left: 20%;
    color: transparent;
    font-weight: bold;
    font-family: sans-serif;
    font-style: italic;
    font-size: 40px;
}